import * as React from "react";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";
import {Paper, Typography, ListItem, ListItemIcon, ListItemText} from "@mui/material";
import {makeStyles} from "@mui/styles";
import {Autorenew, Whatshot, Savings, Shower, AccountBalance, Bolt, SwapHorizontalCircle} from "@mui/icons-material";
import {CoineusCryptoFormat} from "../utils/currency_format";

const useStyles = makeStyles((theme) => ({
    xyplot: {
        margin: '0 auto',
    },
}));

function BurnSupply(props) {
    const classes = useStyles();

    const {burnt, supply, percent, staked, faucet, fund, liquidity} = props;

    const divider = 360 * .0075 * (Math.PI / 180);

    const aSupply= 360 * percent * (Math.PI / 180);

    const percent2 = staked ? (staked - burnt) / supply : 0;
    const aStaked = 360 * percent2 * (Math.PI / 180);

    const burntValue = (percent * 100).toFixed(2);
    const percentStaked = staked ? ((staked / supply) * 100).toFixed(2) : 0;

    const percent3 = faucet / supply;
    const aFaucet = 360 * percent3 * (Math.PI / 180);

    const percent4 = fund / supply;
    const aFund = 360 * percent4 * (Math.PI / 180);

    const percent5 = liquidity / supply;
    const aLiquidity = 360 * percent5 * (Math.PI / 180);

    const colors = {
        label: '#fff',
        supply: '#fbca00',
        burned: '#fa5700',
        staked: '#ff0000',
        faucet: '#1c6fe0',
        fund: '#019f17',
        liquidity: '#a100ff',
    }

    return (
        <Paper style={{padding: 20}}>
            <FlexibleWidthXYPlot
                xDomain={[-5, 5]}
                yDomain={[-5, 5]}
                height={250}
                margin={{left: 0, right: 0, top: 0, bottom: 0}}
                className={classes.xyplot}
            >
                <ArcSeries
                    radiusDomain={[0, 10]}
                    center={{x: 0, y: 0}}
                    data={[
                        {
                            angle0: aSupply+ aStaked + aFaucet + aFund + aLiquidity + (divider*6),
                            angle: Math.PI * 2,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.supply,
                        },
                        {
                            angle0: divider,
                            angle: aFaucet + divider,
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.faucet,
                        },
                        {
                            angle0: aFaucet + (divider*2),
                            angle: aSupply+ aFaucet + (divider*2),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.burned,
                        },
                        {
                            angle0: aSupply+ aFaucet + (divider*3),
                            angle: aSupply+ aStaked + aFaucet + (divider*3),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.staked,
                        },
                        {
                            angle0: aSupply+ aStaked + aFaucet + (divider*4),
                            angle: aSupply+ aStaked + aFaucet + aFund + (divider*4),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.fund,
                        },
                        {
                            angle0: aSupply+ aStaked + aFaucet + aFund + (divider*5),
                            angle: aSupply+ aStaked + aFaucet + aFund + aLiquidity + (divider*5),
                            opacity: 1,
                            radius: 6.75,
                            radius0: 6,
                            color: colors.liquidity,
                        },
                    ]}
                    colorType={'literal'}
                />
            </FlexibleWidthXYPlot>
            <Typography
                style={{
                    height: 0,
                    width: '100%',
                    textAlign: 'center',
                    top: -125,
                    position: 'relative',
                    fontSize: 30,
                    fontWeight: '700',
                }}
            >
                {burntValue}
                <span
                    style={{
                        fontSize: 12,
                        fontWeight: '400',
                        marginTop: -10,
                        display: 'block',
                    }}
                >
          % BURNED
        </span>
            </Typography>
            {staked !== 0 && <Typography
                style={{
                    height: 0,
                    width: '100%',
                    textAlign: 'center',
                    top: -180,
                    position: 'relative',
                    fontSize: 30,
                    fontWeight: '700',
                }}
            >
                {percentStaked}
                <span
                    style={{
                        fontSize: 12,
                        fontWeight: '400',
                        marginTop: -10,
                        display: 'block',
                    }}
                >
          % CNSONE
        </span>
            </Typography>}

            <StatListItem icon={<Bolt style={{color:colors.label}}/>} color={colors.label} value={supply} label="TOTAL SUPPLY" />
            <StatListItem icon={<Shower style={{color:colors.faucet}}/>} color={colors.faucet} value={faucet} label="FAUCET" />
            <StatListItem icon={<Whatshot style={{color:colors.burned}}/>} color={colors.burned} value={burnt} label="BURNED" />
            <StatListItem icon={<Savings style={{color:colors.staked}}/>} color={colors.staked} value={staked} label="COINEUS ONE" />
            <StatListItem icon={<AccountBalance style={{color:colors.fund}}/>} color={colors.fund} value={fund} label="FUND" />
            <StatListItem icon={<SwapHorizontalCircle style={{color:colors.liquidity}}/>} color={colors.liquidity} value={liquidity} label="LIQUIDITY" />
            <StatListItem icon={<Autorenew style={{color:colors.supply}}/>} color={colors.supply} value={supply - staked - burnt - faucet - fund - liquidity} label="CIRCULATING" />
        </Paper>
    );
}

export default BurnSupply;

function StatListItem(props) {
    const {color, value, label, icon} = props;

    return <ListItem disablePadding>
        <ListItemIcon>
            {icon}
        </ListItemIcon>
        <ListItemText
            primary={CoineusCryptoFormat(value)}
            secondary={label}
            primaryTypographyProps={{fontWeight:700, style:{color}}}
            secondaryTypographyProps={{fontSize: '.75rem',style:{color}}}
        />
    </ListItem>;
}
